<template>
<transition name="slide-fade">
	<div class="cc-main">
		<div class="cc-top">
			<div>您好，{{detail.contact}}</div>
			<p>{{detail.province}} {{detail.city}} {{detail.district}}</p>
			<p><van-icon class="cc-cion1" name="wap-home" /> {{detail.name}} <van-icon class="cc-cion2" name="phone" /> {{detail.contactMobile}}</p>
		</div>
		<div class="cc-tit">劳务对接管理</div>
		<van-pull-refresh class="cc-list0" v-model="refreshing" @refresh="onRefresh">
			<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
				<div class="cc-list" v-for="(item,index) in list" :key="index" @click="goto(item)">
					<div class="cc-name">待输出人员 {{item.num}}人</div>
					<div class="cc-address flexstart"><span>{{item.createTime}}</span></div>
					<div class="cc-icon"><van-icon color="#C6C9D2" name="arrow" /></div>
					
				</div>
			</van-list>
		</van-pull-refresh>
		<div class="cc-button"><div @click="goto1">发布需求</div></div>
	</div>
</transition>
</template>
<script type="text/javascript">
import {PullRefresh,List,Icon,Toast} from 'vant';
export default {
	name: 'home',
	components: {
		[PullRefresh .name]: PullRefresh ,
		[List.name]: List,
		[Icon.name]: Icon,
		[Toast.name]: Toast,
	},
	data() {
		return {
			pageNum:1,
			pageSize:6,
			list: [],
			loading: false,
			finished: false,
			refreshing: false,
			detail:""
		};
	},
	mounted() {
		this.detailCompany();
	},
	methods: {
		goto(a){
			let b = JSON.stringify(a);
			console.log(b)
			localStorage.setItem('item1',b);
			this.$router.push({
				path:"/detail2"
			})
		},
		goto1(){
			this.$router.push({
				path:"/publish2"
			})
		},
		//渲染头部
		detailCompany() {
			var that=this;
			this.$http.get('/main/getIntermediaryInfo').then(function (res) {
				if (res.code==10000) {
					that.detail=res.data.intermediary;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		//渲染列表
		onLoad() {
			if (this.refreshing) {
				this.list = [];
				this.refreshing = false;
			}
			var that=this;
			this.$http.get(`/main/intermediaryList/${that.pageNum}/${that.pageSize}`).then(function (res) {
				that.loading = false;
				if (res.code==10000) {
					that.list.push(...res.data.list)
					if (res.data.hasNextPage) {
						that.pageNum=res.data.pageNum+1
					}else{
						that.finished = true;
					}
				}else{
					that.finished = true;
					Toast.fail(res.msg);
				}
			}).catch(function () {
				that.loading = false;
			});
		},
		onRefresh() {
			// 清空列表数据
			this.finished = false;
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.loading = true;
			this.pageNum = 1;
			this.onLoad();
		},
	}
}
</script>
<style lang="scss" scoped>
.cc-main{
	min-height: 100vh;
	background:#fff;
	padding-bottom: 60px;
	.cc-top{
		width: 375px;
		height: 140px;
		padding:24px 27px;
		background:url(https://quickrequest.oss-cn-beijing.aliyuncs.com/image/922cb441badc5483221554e744d4490.png) left top no-repeat;
		background-size: 100% 100%;
		div{
			font-size:22px;
			font-weight:800;
			color:rgba(255,255,255,1);
			line-height:30px;
			margin-bottom: 10px;
		}
		p{
			font-size:13px;
			color:rgba(255,255,255,0.85);
			line-height:18px;
			margin-bottom: 9px;
			.cc-cion1{
				margin-right:4px;
			}
			.cc-cion2{
				margin:0 4px 0 16px;
			}
		}
	}
	.cc-button{
		position: fixed;
		left: 0;
		bottom: 0;
		width: 375px;
		height: 60px;
		background:#fff;
		padding:8px 16px;
		border-top: 1px solid #eee;
		div{
			width:343px;
			height:44px;
			background:linear-gradient(314deg, rgba(30,151,168,1) 0%, rgba(52,174,191,1) 100%);
			border-radius:2px;
			line-height: 44px;
			text-align: center;
			font-size:18px;
			color:rgba(255,255,255,1);
		}
	}
	.cc-tit{
		padding:20px 16px 15px;
		border-bottom: 1px solid #eee;
		font-size:18px;
		font-weight:600;
		color:rgba(19,37,55,1);
		line-height:25px;
	}
	.cc-list0{
		padding:0 16px;
	}
	.cc-list{
		padding:20px 0;
		border-bottom: 1px solid #eee;
		position: relative;
		.cc-name{
			font-size:16px;
			font-weight:500;
			color:rgba(19,37,55,1);
			line-height:18px;
			margin-bottom: 12px;
			padding-left: 12px;
			border-left: 3px solid #2FA9BA;
		}
		.cc-address{
			padding-left: 12px;
			font-size:12px;
			font-weight:300;
			color:rgba(123,130,154,0.85);
			line-height:17px;
		}
		.cc-icon{
			position: absolute;
			top:37px;
			right: 0;
		}
	}
}
</style>